.about {
    margin: 0 1rem;
  }
  
  .about-container {
    margin: 0 1rem;
    outline: 4px solid #E02844;
    border-radius: 1.6rem;
    background-color: #212529;
    padding: 2rem;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
  }

.about-subtitle {
margin-top: 0;
margin-bottom: 1.5rem;
font-size: 2.5rem;
font-weight: bold;

}


.about-text {
margin: 0% 5%;
color: #f2f2f2; /* a lighter shade of white /
line-height: 1.5; / increase line height for better readability */
}

